@import url(https://fonts.googleapis.com/css2?family=Acme&display=swap);
:root {
  --prim: #7f8fa3;
  --sec: #ffa0a3;
  --accOne: #77ddf9;
  --accTwo: #eafbff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  width: 100vw;
  height: 100vh;
}

body {
  width: 100%;
  min-height: 100vh;
  background-color: #77ddf9;
  background-color: var(--accOne);
  font-family: "Acme", sans-serif;
}

#root {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App {
  display: grid;
  width: 100%;
  max-width: 1130px;
  height: 40rem;
  grid-template-columns: 40rem minmax(430px, 1fr);
  grid-template-rows: 1fr 12rem;
  grid-row-gap: 1rem;
  row-gap: 1rem;
  grid-column-gap: 2rem;
  -webkit-column-gap: 2rem;
          column-gap: 2rem;
}

.gameboardWrapper {
  height: 40rem;
  width: 40rem;
  align-self: center;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  grid-row: span 2;
}

@media screen and (max-width: 1280px), (max-height: 720px) {
  .App {
    height: 30rem;
    max-width: 900px;
    grid-template-columns: 30rem 1fr;
    grid-template-rows: 1fr 8rem;
  }
  .gameboardWrapper {
    height: 30rem;
    width: 30rem;
  }
}

.animatedGameboard {
  width: 40rem;
  height: 40rem;
}
.GameboardContainer {
  width: 100%;
  height: 100%;
}
.Gameboard {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-columns: repeat(var(--gameboardSize), 1fr);
  grid-template-rows: repeat(var(--gameboardSize), 1fr);
  grid-gap: 1rem;
  gap: 1rem;
}

/*MEDIA QUERY**********************************************************/
@media screen and (max-width: 1280px), (max-height: 720px) {
  .animatedGameboard {
    width: 30rem;
    height: 30rem;
  }
}

.Card {
  background-color: transparent;
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

.CardInner {
  transition: -webkit-transform 0.5s cubic-bezier(0.73, -0.53, 0.87, 0.37);
  transition: transform 0.5s cubic-bezier(0.73, -0.53, 0.87, 0.37);
  transition: transform 0.5s cubic-bezier(0.73, -0.53, 0.87, 0.37), -webkit-transform 0.5s cubic-bezier(0.73, -0.53, 0.87, 0.37);
  transition-delay: calc(var(--order) * 0.08s);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  width: 100%;
  height: 100%;
}

.CardFront,
.CardBack {
  border-radius: 0.5rem;
  overflow: hidden;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
  box-shadow: 2px 2px 6px 1px rgba(0, 0, 0, 0.4);
}

.CardBack {
  background-color: var(--sec);
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  position: relative;
}

.circle1 {
  position: absolute;
  width: 125%;
  height: 125%;
  border-radius: 50%;
  left: 30%;
  top: 30%;
  background-color: var(--prim);
  box-shadow: -2px -2px 6px rgba(0, 0, 0, 0.15);
}

.circle2 {
  position: absolute;
  width: 90%;
  height: 90%;
  left: 48%;
  top: 48%;
  border-radius: 50%;
  background-color: var(--accOne);
  box-shadow: -2px -2px 6px rgba(0, 0, 0, 0.15);
}

.circle3 {
  position: absolute;
  width: 55%;
  height: 55%;
  left: 65%;
  top: 65%;
  border-radius: 50%;
  background-color: var(--accTwo);
  box-shadow: -2px -2px 6px rgba(0, 0, 0, 0.15);
}

.CardInner:hover > .CardFront::after {
  position: absolute;
  left: 0;
  top: 0;
  content: "";
  background-color: transparent;
  background: linear-gradient(
    0deg,
    rgba(0, 0, 0, 0.541) 0%,
    rgba(0, 0, 0, 0) 30%
  );
  width: 100%;
  height: 100%;
}

.CardFront > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.photographerLink {
  z-index: 1;
  display: none;
  position: absolute;
  text-align: center;
  padding: 5% 0;
  width: 100%;
  bottom: 0;
  left: 0;
  color: white;
}

.CardFront:hover > .photographerLink {
  display: block;
}

.showBack > .CardInner {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.Gameover {
  width: 100%;
  height: 100%;
  position: relative;
}
.gameOverCircle1 {
  background-color: var(--prim);
  width: 80%;
  height: 80%;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-shadow: 2px 2px 6px 1px rgba(0, 0, 0, 0.4);
}
.gameOverCircle2 {
  background-color: var(--accTwo);
  width: 60%;
  height: 60%;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-shadow: 2px 2px 6px 1px rgba(0, 0, 0, 0.4);
}
.gameOverCircle3 {
  background-color: var(--sec);
  width: 40%;
  height: 40%;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-shadow: 2px 2px 6px 1px rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.gameOverCircle3 > h2 {
  color: var(--accTwo);
  font-size: 2rem;
  margin-bottom: 1rem;
}
.gameOverCircle3 > button {
  border: none;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  background-color: var(--accTwo);
  color: var(--prim);
  font-family: "Acme", sans-serif;
  font-size: 1.1rem;
  cursor: pointer;
}
.gameOverCircle3 > button:hover {
  background-color: var(--prim);
  color: var(--accTwo);
}

/*HOW TO PLAY STYLING****************************************/
.HowToPlay {
  background-color: var(--prim);
  padding: 2rem 3rem;
  width: 100%;
  text-align: justify;
  border-radius: 0.5rem;
  line-height: 145%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: var(--accTwo);
  font-size: 1.3rem;
  box-shadow: 2px 2px 6px 1px rgba(0, 0, 0, 0.4);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.HowToPlay > h2 {
  text-align: center;
  font-size: 3rem;
  margin: 1rem 0;
}
.Links {
  height: 2rem;
}
.Links > a {
  height: 100%;
  margin-left: 1rem;
}
.Links > a > img {
  height: 100%;
  object-fit: scale-down;
}

/*SETTINGS STYLING*******************************************/
.settings {
  background-color: var(--prim);
  width: 100%;
  padding: 2rem 3rem;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--accTwo);
  box-shadow: 2px 2px 6px 1px rgba(0, 0, 0, 0.4);
}
.score {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 2rem;
  font-size: 1.5rem;
}
.diffSettings {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

/*BUTTONS STYLING*************************************************/
.diffSettings > button {
  font-family: "Acme", sans-serif;
  background-color: var(--accOne);
  border: none;
  padding: 0.5rem 1rem;
  font-size: 1.2rem;
  cursor: pointer;
  width: 6rem;
  font-size: 1.1rem;
  border-radius: 0.5rem;
  color: var(--accTwo);
}
.diffSettings > button:hover {
  background-color: var(--sec);
}

/*MEDIA QUERY*************************************/
@media screen and (max-width: 1280px), (max-height: 720px) {
  .HowToPlay {
    font-size: 1rem;
  }
  .HowToPlay > h2 {
    font-size: 2.5rem;
  }
  .score {
    font-size: 1rem;
  }
  .diffSettings > button {
    font-size: 0.8rem;
    width: 4rem;
    padding: 0.25rem 0.5rem;
  }
  .Links {
    height: 1.3rem;
    margin-top: 0.3rem;
  }
}

